import React, { useState, useEffect } from "react";
import ReactDOM from "react-dom";
import "../node_modules/bootswatch/dist/lux/bootstrap.css";

function App() {
  const [Data, setData] = useState([]);

  useEffect(() => {
    fetchData();
  }, []);

  function fetchData() {
    fetch("https://ghibliapi.herokuapp.com/films/")
      .then(resp => resp.json())
      .then(data => setData(data));
  }

  return (
    <div className="container text-center mb-5">
      <h1 className="m-3">Anime Movie List</h1>
      <div className="row ">
        {Data.map((data, index) => (
          <div className="col-md-6 p-1 ">
            <div className="card  m-2 p-4 shadow-sm">
              <h5 className="p-3">{data.title}</h5>
              <div className="row ">
                <div className="col-4">
                  <p className="text-secondary">
                    Year <br />
                    {data.release_date}
                  </p>
                </div>
                <div className="col-4">
                  <p className="text-secondary">
                    Rotten Tomatoes <br /> {data.rt_score}%
                  </p>
                </div>
                <div className="col-4">
                  <p className="text-secondary">
                    Director <br />
                    {data.director}
                  </p>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
